.navBar, .navigation {
  padding: 10px 15px;
}
.navBar {
  width: 100%;
  -ms-flex-pack: justify;
  justify-content: space-between;
  position: fixed;
  display: -ms-flexbox;
  display: flex;
}
.logo {
  font-family: arial;
  font-weight: 700;
}
.logo {
  width: 65px;
  height: 65px;
}
.content {
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: center;
  justify-content: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
}
.rect {
  height: 575px;
  width: 225px;
  z-index: 0;
  border-width: 5px;
  border-color: rgba(2, 130, 119, 0.4);
  border-style: solid;
  position: absolute;
}
.hello {
  font-size: 50px;
  z-index: 1;
  color: #333;
  font-weight: 100;
  margin-bottom: -15px;
}
.text {
  font-size: 75px;
  color: #333;
  z-index: 1;
}
.name {
  font-weight: 900;
  color: #000;
  font-size: 100px;
}
.highlight {
  font-weight: 800;
  color: #000;
  letter-spacing: 5px;
}
.cover {
  display: -ms-flexbox;
  display: flex;
  background: -webkit-radial-gradient(circle,#fff,#f0f0f0);
  background: -o-radial-gradient(circle,#fff,#f0f0f0);
  background: radial-gradient(circle,#fff,#f0f0f0);
  height: 100vh;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: center;
  justify-content: center;
}
.mail {
  font-size: "16px";
  letter-spacing: "5px";
  font-size: 75px;
  z-index: 999;
  text-decoration-line: "none";
}
text {
  font-family: Arial,Helvetica,sans-serif;
  letter-spacing: 8px;
}
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}